* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2em;
  width: 100%
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  border-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card h4 {
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}